.blurBackRequest{
    position: fixed;
    z-index: 1000000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
}

.requestSectionCanvs{
    position: absolute;
    width: 400px;
    background-color: #fff;
    height: 100%;
    right: 0;
    animation: animationDropCenter .3s ;
}

.closeIcoRequest{
    cursor: pointer;
}

@keyframes animationDropCenter {
    0% {
        right:-400px;
    }
}

@media(max-width:720px){
    .requestSectionCanvs{
        width:100%
    }
}