//     font-weight: 600;
//     text-align: center;
// }

.btnflexx{
    width: 50%;
    padding-left: 70px;
    margin-top: auto;
    margin-bottom: auto;
    font-size: 13px;
}
.startedbtn{
    font-family: Roboto;
    border: 1px solid #007DFF;
    outline: none !important;
    width: 150px;
    padding: 10px;
    border-radius: 40px;
    background-color: #007DFF;
    color: white;
    cursor: pointer;
}
.startedbtn:hover{
    border: 1px solid #1B6FC9;
  background-color: #1B6FC9;
}
.demobtn{
    font-family: Roboto;
    border: 1px solid rgba(9, 28, 63, 0.25);
    outline: none !important;
    width: 150px;
    padding: 10px;
    border-radius: 35px;
    background-color: white;
    color: #091C3F;
    margin-left: 30px;
    cursor: pointer;
}
.demobtn:hover{
  border: 1px solid #007CFF;
  color: #007CFF;
}
.mobtext{
    display: none;
}

.infodiv{
    width: 50%;
    margin: auto;
}

.care-sub{
    font-family: Roboto;
    color: rgba(9, 28, 63, 0.6);
    font-size: 18px;
}
.wrp-req-div{
    width: 520px;
}


@media(max-width: 800px){

    .wrp-req-div{
        width:100%
    }

    .aac{
        text-align: center;
        width: 80%;
        margin: auto;
    }
    .demoflexx{
        padding: 35px;
        padding-left: 35px;
        padding-right: 35px;
    }
    .care-sub{
        text-align: center;
    }
    .demoflexx{
        flex-direction: column;
    }
    .infodiv{
        width: 100%;
        // text-align: center;
    }
    .btnflexx{
        width: 100%;
        padding-left: 0px;
        margin: auto;
        margin-top: 20px;
    }
    .demodiv{
        margin-bottom: 20px;
    }
    .demobtn{
        margin: 5px;
        margin-top: 18px;
        // display: none;
    }
    .mobtext{
        width: 80%;
        margin: auto;
        text-align: center;
        display: block;
        margin-bottom: 20px;
        margin-top: 0px;
    }
    .care-head {
        font-family: Gilroy;
        width: 100%;
        margin: auto;
        text-align: center;
    }
    .wrp-req-div{
        width: 100%;
        margin: auto;
    }
    .btn-wrp{
        text-align: center;
    }
}
