

.hmPgReqDmSecCls .com-section{
    display: flex;
    margin-top: 30px;
}

.hmPgReqDmSecCls .erri{
    color:red;
    padding-top: 4px;
}
.hmPgReqDmSecCls .img-section{
    width: 50%;
    text-align: center;
    // float: right;
    margin-top: 70px;
}
.hmPgReqDmSecCls .addtoRight{
    float: right;
    margin-right: 80px;
}
.hmPgReqDmSecCls .grp-img{
    height: 250px;
    text-align: center;
}
.hmPgReqDmSecCls .header-div{
    width: 350px;
    font-family: Gilroy;
    margin: auto;
    text-align: center;
    color: #091C3F;
    font-size: 24px;
    font-weight: 800;
}
.hmPgReqDmSecCls .subhead-divv{
    width: 350px;
    margin: auto;
    text-align: center;
    color: #091C3F;
    font-size: 18px;
    margin-top: 10px;
}
.hmPgReqDmSecCls .input-section{
    width: 50%;
    padding-left: 80px;
}

.hmPgReqDmSecCls .quote-txt{
    color: #091C3F;
    font-size: 22px;
    font-family: Gilroy;
    font-weight: 800;
    text-align: center;
    margin-top: 20px;

}
.hmPgReqDmSecCls .outer-divv{
    width: 315px;
    height: 45px;
    background: #F5F9FF;
    border: 1px solid rgba(9, 28, 63, 0.1);
    border-radius: 6px;
}
.hmPgReqDmSecCls .wrp-clss{
    width: 65%;
    margin: auto;
    height:70px;
    margin-top: 20px;
}
.hmPgReqDmSecCls .label-name{
    color: #091C3F;
    font-size: 14px;
    margin-bottom: 3px;
}
.hmPgReqDmSecCls .name-input{
    border: none;
    outline: none;
    width: 280px;
    height: 40px;
    background-color: #F5F9FF;
    padding-left: 20px;
}
