//     height: 45px;
//     background: #1987FB;
//     border-radius: 40px;
//     color: white;
// }

.hmPgReqDmSecCls .qoute-div{
    position: absolute;
    top: 50%;
    left: 50%;
    width: fit-content;
    transform: translate(-50%, -50%);
}

.hmPgReqDmSecCls .wrapperinpsec{
    width: 400px;
}
@media(max-width: 720px){
    .hmPgReqDmSecCls .com-section{
        flex-direction: column;
        margin-top: 30px;
    }

    .hmPgReqDmSecCls .wrapperinpsec{
        width: 100%;
    }

    .hmPgReqDmSecCls .addtoRight{
        float: none;
        margin-right: 0px;
    }

    .hmPgReqDmSecCls .img-section{
        width: 100%;
        margin: auto;
        margin-top: 30px;
    }
    .hmPgReqDmSecCls .input-section{
        width: 100%;
        padding-left: 0px;
    }
    .hmPgReqDmSecCls .grp-img{
        height: 200px;
        margin-top: 20px;
        text-align: center;
    }
    .hmPgReqDmSecCls .qoute-div{
        width: 100%;
        margin: auto;
    }
}
